<template>
  <container-block class="mt-4 mb-4">
    <div class="col-12 col-lg-4">
      <div class="img-tag user">
        <div
          class="user__img"
          @mouseenter="uploadImg = true"
          @mouseleave="uploadImg = false"
          @click="uploadButton"
        >
          <div class="user__img__upl" :style="{ opacity: uploadImg ? 1 : 0 }">
            Загрузить фото
          </div>
        </div>
        <div class="text-center" v-if="uploadNamePhoto !== ''">
          Будет загружен файл: {{ uploadNamePhoto }}
        </div>
        <form v-show="false" ref="form-img">
          <input type="file" ref="input-img" @change="uploadImgEvent" />
        </form>
        <div class="user__name">Иван Иванов, 20</div>
      </div>
    </div>
    <div class="col-12 col-lg-8">
      <form-component />
    </div>
  </container-block>
</template>

<script>
import FormComponent from "../components/profile/formComponent.vue";

export default {
  components: {
    FormComponent,
  },
  data: () => ({
    uploadImg: false,
    uploadNamePhoto: "",
    tab: "",
  }),
  created() {
    if (this.$route.query.tab !== undefined) {
      this.tab = parseInt(this.$route.query.tab, 10);
    }
  },
  methods: {
    uploadButton() {
      this.$refs["form-img"].reset();
      this.$refs["input-img"].click();
    },
    uploadImgEvent(e) {
      const nameFile = e.target.files[0].name;
      this.uploadNamePhoto = nameFile;
      // TODO Загрузка файла.
    },
  },
};
</script>

